body {
    margin: 0;
    padding: 0;
    color: #fff;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: black;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

h4 {
    font-style: italic;
    margin-top: -15px;
}

 
#outer-container {
    background-image: linear-gradient(rgba(0, 0, 0, 0.9) 35%, rgba(0, 0, 0, 0.65) 70%, rgba(0, 0, 0, 1) 100%);
    background-attachment: scroll;
    margin: 0;
    height: 120vh;
}


.App {
    -webkit-animation: fadein ease-out 1.4s;
            animation: fadein ease-out 1.4s;
}


.App-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-top: 2%;
    padding-left: 5%;
}

.App-logo {
    width: 150px;
}


.logo-link:focus {
    outline: none;
}

.logo-link:focus,
.logo-link:hover>img {
    -webkit-filter: brightness(300%);
            filter: brightness(300%);
}

hr {
    border: 1px solid #01d277;
    width: 90%;
    position: absolute;
    margin-top: 75px;
    margin-bottom: 75px;
}

/* Search Form */
.search-form {
    position: relative;
}

input {
    background-color: transparent;
    color: #ffffff;
    font-size: 18px;
    padding: 10px 10px 10px 10px;
    width: 350px;
    border: none;
    border-bottom: 1px solid #01d277;
}

input:focus {
    outline: none;
}



/* Label */
label {
    color: #edfff7;
    font-style: italic;
    font-size: 18px;
    opacity: 0.5;
    position: absolute;
    top: 15px;
    left: 10px;
    transition: 0.2s ease all;
    pointer-events: none;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

/* Label Active State */
input:focus~label {
    top: -17px;
    left: 0px;
    font-style: normal;
    opacity: 1.0;
    font-size: 14px;
    color: #ffff;
}

/* Bottom Bar */
.search-bar {
    position: relative;
    display: block;
    width: 100%;
}

.search-bar:before,
.search-bar:after {
    content: '';
    height: 2px;
    width: 0;
    position: absolute;
    background: #01d277;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.search-bar:before {
    left: 50%;
}

.search-bar:after {
    right: 50%;
}

/* Bottom Bar Active State */
input:focus~.search-bar:before,
input:focus~.search-bar:after {
    width: 50%;
}

/* Button */

.search-button {
    position: absolute;
    border: 2px solid #01d277;
    border-radius: 10px;
    font-size: 18px;
    padding: 0 5% 0 5%;
    left: 380px;
    top: 18px;
    background-color: transparent;
    color: #01d277;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.search-button:focus {
    outline: none;
    color: #fff;
    border: 2px solid #ffff;
}

.search-button:after {
    content: '\279E';
}

.search-button:hover {
    background-color: #01d277;
    color: #081c24;
}

.search-button:active {
    background-color: #ffff;
    color: #081c24;
}


/* Media Queries */
@media only screen and (max-width: 650px) {
    .App-header {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
    }

    .App-logo {
        width: 65vw;
        margin-left: 5%;
        margin-right: 10%;
    }

    .search-form {
        margin-right: 15%;
    }

    input {
        font-size: 18px;
        width: 200px;

    }

    .search-button {
        padding: 0 15px 0 15px;
        left: 225px;
        top: 16px;
    }

    hr {
        display: none;
    }


}
/* Entire Movie Page */

.movie-container {
    padding: 0 5% 0 5%;
    min-width: 850px;
    width: 70%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

/* ============= Movie Poster & Details ============= */

/* container */
.movie {
    height: 45vh;
    margin-top: 30px;
    margin-bottom: 25px;
    overflow: visible;
}

.movie-page-poster {
    height: 100%;
    float: left;
    margin-right: 30px;
    border-radius: 10px;
    border: 1px solid #fff;
    background-color: #cbcbcb;
}

/* Overall Movie Data */
.movie-data {
    display: flex;
    flex-wrap: wrap;
}

.movie-headline {
    flex-basis: 100%;
}

/* Details & Plot */
.movie-details {
    flex-basis: 30%;
}

.movie-plot {
    flex-basis: 70%;
}

/* ============= Links ============= */
.genre-link>a {
    color: #01d277;
}

.genre-link>a:visited {
    color: #b2b2b2;
}

.genre-link>a:hover {
    text-decoration: none;
}

.genre-link>a:active {
    color: #fff;
}

.cast-container>a:hover,
.similar-movie-container>a:hover {
    color: #01d277;
}

.cast-container>a,
.similar-movie-container>a {
    text-decoration: none;
    color: #fff;
}


/* ============= Cast & Similar Movie Sliders ============= */
.cast-container,
.similar-movie-container {
    overflow: hidden;
    height: 40vh;
    padding-left: 10px;
    margin-right: -15px;
    margin-left: 15px;
}

.cast-container img:hover,
.similar-movie-container img:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}


.cast-photo,
.similar-movie-photo {
    height: 75%;
}

.cast-nophoto,
.similar-movie-nophoto {
    height: 75%;
    background-color: #cbcbcb;
}


.actor,
.similar-movie-title {
    font-weight: bold;
    margin-bottom: -10px;
}

.actor,
.character,
.similar-movie-title {
    width: 90%;
}

/* ============= Video Sliders ============= */

.video-container {
    margin-left: 15%;
    margin-bottom: 15%;
}

.video-list>button {
    top: 33%;
}

.video-list>.slick-next {
    right: 5%;
}

.video-list>.slick-prev {
    left: 10%;
}

/* ============= Slick Slider Styling ============= */
.slick-next,
.slick-prev {
    position: absolute;
    font-size: 0;
    top: 38%;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    cursor: pointer;
    border: none;
    background: 0 0;
    opacity: .75;
    color: #fff;
    transition: all .2s ease-in-out;
}

.slick-next:focus:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
    opacity: 1;
    font-size: 40px;

}

.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
    opacity: .25
}

.slick-next:before,
.slick-prev:before {
    font-size: 30px;
    line-height: 1;
    opacity: .75;
    color: #fff
}

.slick-prev {
    left: -30px;
}

.slick-next {
    right: -35px
}

.slick-prev:before {
    content: '\AB'
}


.slick-next:before {
    content: '\BB'
}


.slick-list,
.slick-slider,
.slick-track {
    position: relative;
}

.slick-list {
    overflow: hidden;
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand
}

.slick-slider .slick-list,
.slick-slider .slick-track {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0)
}


.slick-slide {
    float: left;
}

.slick-slide.dragging img {
    pointer-events: none
}

/* ============= Media Queries ============= */

@media only screen and (max-width: 450px) and (min-height: 800px) {
    .movie-container {
        padding: 0 5% 0 5%;
        min-width: 85vw;
        width: 60%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }

    .movie,
    .movie-page-poster,
    .movie-data {
        width: 100%;
    }

    .movie {
        height: 80vh;
        margin-bottom: 20%;
    }

    .movie-page-poster {
        margin-right: 0px;
        height: auto;
    }

    .movie-data {
        text-align: center;
    }

    .movie-details,
    .movie-plot {
        flex-basis: 100%;
    }

    .cast-container,
    .similar-movie-container {
        height: 50vh;
        padding-left: 0;
        margin-left: 0;
        margin-right: 0;
    }

    .cast {
        margin-top: 50vh;
    }

    .video-list, .video-list>.slick-list {
        width: 350px !important;
        height: auto;
        margin-bottom: 10vh;
    }

   .video-container>iframe {
        width: 350px !important;
        height: 197px !important;
    }

    .video-container {
        margin: 3% 0 0 0;
    }

    .video-list>button {
        top: -1%;
    }

    .video-list>.slick-next {
        left: 50%;
    }

    .video-list>.slick-prev {
        left: 40%;
    }

    .slider-list {
        margin: 0 auto;
    }

    .slick-list,
    .slick-slider,
    .slick-track {
        width: 80%;
    }

    .slick-list {
        display: block;
        margin: 0 auto;
    }

    .slick-next {
        right: 0vh
    }

    .slick-prev {
        left: -2vh;
    }
}


